<template>
  <el-dialog
  :title="title"
  :visible.sync="dialogVisible"
  width="400px"
  :before-close="onClear"
  >
  <el-form ref="form" :model="form" :rules="rules" label-width="80px">
    <el-form-item label="学科名称" prop="subjectName">
      <el-input v-model="form.subjectName" placeholder="请输入学科名称">
      </el-input>
    </el-form-item>
    <el-form-item label="是否显示">
      <!-- inactive-value组件Switch关闭时的值 active-value打开的值-->
      <el-switch
        v-model="form.isFrontDisplay"
        active-color="#13ce66"
        inactive-color="#ff4949"
        >
      </el-switch>
    </el-form-item>
  </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="onClear">取 消</el-button>
    <el-button type="primary" @click="onAdd()">确 定</el-button>
  </span>
</el-dialog>
</template>

<script>
import { add, update, detail } from '@/api/hmmm/subjects'
export default {
  name: 'getDialog',
  props: {
    list: Object
  },
  data () {
    return {
      form: {
        subjectName: '',
        isFrontDisplay: null
      },
      dialogVisible: false,
      input: '',
      type: 'add',
      rules: {
        subjectName: [
          { required: true, message: '请输入学科名称', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    // this.edit()
  },
  computed: {
    title () {
      const title = ''
      if (this.type === 'add') return '新增学科'
      if (this.type === 'edit') return '修改学科'
      return title
    }
  },
  methods: {
    // 打开弹窗
    async open () {
      this.dialogVisible = true
      this.type = this.list.type
      // console.log(this.list.row.id)
      // 回显
      if (this.type === 'edit') {
        const res = await detail({ id: this.list.row.id })
        // console.log(res)
        this.form.subjectName = res.data.subjectName
        this.form.isFrontDisplay = res.data.isFrontDisplay === 1
      }
    },
    // 点击确定
    async onAdd () {
      // console.log(this.list)
      await this.$refs.form.validate()
      console.log(this.type)
      if (this.type === 'add') {
        await add(this.form)
        this.$message({
          type: 'success',
          message: '操作成功!'
        })
        // console.log(res)
      } else if (this.type === 'edit') {
        await update({
          id: this.list.row.id,
          ...this.form
        })
        this.$message({
          type: 'success',
          message: '操作成功!'
        })
      }
      // 子传父  渲染
      this.$emit('addon')
      this.onClear()
    },
    // 清空表单
    onClear () {
      this.dialogVisible = false
      this.form = {
        subjectName: '',
        isFrontDisplay: true
      }
    }
  }
}
</script>

<style scoped lang='less'></style>
